import React, { Component } from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import './scss/style.scss';
window.server = 'https://kantahsalatiga.com/'
// window.server = "http://localhost/"

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

// Containers
const TheLayout = React.lazy(() => import('./containers/TheLayout'));

// Pages

const Login = React.lazy(() => import('./views/pages/login/Login'));
const LoginAdmin = React.lazy(() => import('./views/pages/loginadmin/LoginAdmin'));
const Logout = React.lazy(() => import('./views/pages/logout/Logout'));
const Register = React.lazy(() => import('./views/pages/register/Register'));
const Page404 = React.lazy(() => import('./views/pages/page404/Page404'));
const Page500 = React.lazy(() => import('./views/pages/page500/Page500'));
const Export = React.lazy(() => import('./views/laporan/ExportLaporan'));
const ExportAdmin = React.lazy(() => import('./views/laporan/ExportLaporanAdmin'));


class App extends Component {

  render() {
    return (
      <HashRouter>
          <React.Suspense fallback={loading}>
            <Switch>
              <Route exact path="/login" name="Login Page" render={props => <Login {...props}/>} />
              <Route exact path="/admin" name="Login Admin" render={props => <LoginAdmin {...props}/>} />
              <Route exact path="/logout" name="Logout Page" render={props => <Logout {...props}/>} />
              <Route exact path="/export" name="Export Page" render={props => <Export {...props}/>} />
              <Route exact path="/exportadmin" name="Export Admin" render={props => <ExportAdmin {...props}/>} />
              <Route exact path="/register" name="Register Page" render={props => <Register {...props}/>} />
              <Route exact path="/404" name="Page 404" render={props => <Page404 {...props}/>} />
              <Route exact path="/500" name="Page 500" render={props => <Page500 {...props}/>} />
              <Route path="/" name="Home" render={props => <TheLayout {...props}/>} />
            </Switch>
          </React.Suspense>
      </HashRouter>
    );
  }
}

export default App;
